<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col-md-12">
                    <div class="element-wrapper">
                        <h2 class="element-header">
                           Empresas
                        </h2>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-xs-12 col-sm-6 col-md-4 col-lg-3 text-center" v-for="company in companies">
                    <element-box class="company-box profile-tile-box">
                        <div class=" box-img" >
                                <img :src="company.photo_url" alt="" class="rounded-circle img-fluid" @click.prevent="$router.push({name:'company.show',params:{id:company.id}})">
                        </div>
                       <div class="box-content">
                           <div class="box-title">
                               <h1 v-text="company.display_name" class="h3 text-grey m-t m-b "></h1>
                           </div>
                           <div class="box-markets">
                               <div class="badge badge-primary bg-grey" v-for="market in ['sertor elétrico','viagens','comércio','alimentação','educação','saúde']" v-text="market"></div>

                           </div>
                       </div>
                            <router-link class="btn btn-primary btn-lg " :to="{name:'company.show',params:{id:company.id}}">
                                <span>Visitar</span>
                            </router-link>
                    </element-box>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import companyMixis from '../../../mixins/companies';
    import handleErrorsMixins from '../../../mixins/handle_errors';

    export default {
       mixins:[companyMixis,handleErrorsMixins],
        created(){
            this.preload(true);
            this.getCompanies().then(()=>{
                this.preload(false);
            }).catch((e)=>{
                this.preload(false);
                this.handleErrors(e);
            })
        },
    }
</script>
<style scoped lang="scss">
    .element-wrapper{
        padding-bottom: 10px;
    }
    .company-box{
        h1{
            word-wrap: break-word;
        }
        $baseHeight:25em;
        &:hover{
            transform: translateY(-5px) scale(1.02);
            box-shadow: 0 5px 12px rgba(126, 142, 177, 0.2);
            transition: all 0.25s ease;
        }
        .box-content{
            $boxContentHeight : $baseHeight/2;
            min-height:$boxContentHeight ;
            .box-title,.box-markets{
                min-height: $boxContentHeight/3;
            }
        }
        .box-markets{
            /*padding-top: 20px;*/
        }

        .box-img{
            margin-left: 25%;
            margin-right: 25%;
            img{
                width: 100px;
                &:hover{
                    cursor: pointer;
                }
            }
        }
    }
    .badge{
        margin: 3px 5px 0px 3px;
    }
    .element-box{
        padding: 1.5rem 0rem;
    }

</style>
