export default{
    methods:{
        getCompanies(){
            return this.$store.dispatch('getCompanies');
        },
        getCompany(id){
            return this.$store.dispatch('getCompany',id);
        },
    },
    computed:{
        companies(){
            return this.$store.state.company.list
        },
        company(){
            return this.$store.state.company.current
        },

    }
}